body {
  margin: 0;
  background-color: #C2CAE8; 
}

#name { 
  position: -webkit-sticky; 
  position: sticky; 
  top: 0;  
  padding: 0.15em; 
  font-family: 'Montserrat', sans-serif;
  font-size: 30px; 
  font-weight: 900; 
  color: #111d4A;  
  letter-spacing: 8px;
  text-align: center; 
  background-color: #C2CAE8; 
  z-index: 1; 
}
canvas {
    display: block;
  }
  
  #particles {
    position: static; 
    background-color: #111d4A;  
    height: 83vh; 
    width: 100%; 
    z-index: -1;
  }
  
  #space-header {
    position: absolute; 
    width: 65%; 
    top: 25%; 
    left: 25%; 
    margin-left: -110px; 
    text-align: center; 
    font-family: 'Archivo Black', sans-serif;
    z-index: 0; 
  }
  
  #space-header h1 { 
    font-size: 85px; 
    color: #fff; 
  }
  
  #space-header p {
    color: #fff; 
  }
#navbar {
    top: 2.5em; 
}

.tabs-container {
    position: -webkit-sticky;
    position: sticky; 
    border-color:#8380B6; 
    border-radius: 0.1rem; 
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 10vh;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
    background: #8789c0;
    z-index: 0;
}

.tab {
    display: flex;
    justify-content: center;
    align-items: center;
    flex: 1 1;
    color:white;
    letter-spacing: 0.1rem;
	transition: all 0.5s ease;
    font-size: 0.8rem; 
    cursor: pointer; 
}

.tab:hover {
  color:white;
  background: #ABACD3; 
  transition: all 0.5s ease;
}

.active {
  background-color: #ABACD3;
}
.tabs,
.slide {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      height: 100vh;
      text-align: center;
      padding: 0 2em;
  }
  
  .slide h1 {
    font-size: 2rem;
    margin: 0;
    letter-spacing: 1rem;
  }

  #tab-resume {
    height: 205vh;
  }

  #frame {
    width: 100%;
    height: 100%;
    overflow: hidden; 
  }

  iframe {
    margin: 0; 
    padding: 0; 
    border: 0; 
  }
footer {
    margin: 0;
    height:auto; 
    background-color: #111d4A;
    color: #EEF0F9; 
}

.flex-container {
    margin: 0; 
    margin-left: 9em; 
    margin-right: 9em; 
    padding: 0.15em; 
    display: flex; 
    justify-content: space-evenly;
}

a {
    text-decoration: none; 
    color: #EEF0F9;
}

a:visited {
    color: #EEF0F9;
}

.icon:hover {
  cursor: pointer;
  color:white;
}
